
.song-list{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
  padding-bottom: 1.56rem;
}
/*header start*/
.song-list .header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 1.29rem;
  padding: 0 0.24rem;
  z-index: 99;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.song-list .header .search-bank{
  width: 0.61rem;
  height: 0.61rem;
  background: url(../assets/image/icon-back.png) no-repeat;
  background-size: 100%;
}
/*header end*/
/*song-list start*/
.song-list-warp{
  padding-bottom: 0.24rem;
}
.song-list-ban{
  width: 100%;
  position: relative;
}
.song-list-ban img{
  width: 100%;
}
.song-list-ban .song-list-info{
  background: rgba(0,0,0,.5);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 11;
  padding: 0.29rem 0.44rem;
}
.song-list-title{
  font-size: 0.44rem;
  color: #fff;
  line-height: 1.5;
}
.song-list-time{
  font-size: 0.29rem;
  color: #fff;
  line-height: 1.5;
}
/*song-list end*/
/*song-list-box start*/
.song-list-box{
  padding: 0.24rem 0.37rem;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  background: url(../assets/image/count-box-border.png) no-repeat bottom;
  background-size: 100% auto;
  -ms-flex-align: center;
      align-items: center;
}
.song-box-index{
  font-size: 0.34rem;
  color: #fff;
  padding-right: 0.24rem;
}
.song-box-info{
  -o-flex: 1;
  -moz-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.song-box-title{
  font-size: 0.34rem;
  margin-bottom: 0.12rem;
  color: #fff;
}
.song-box-name{
  font-size: 0.29rem;
  color: #c2c8d2;
}
.song-box-but{
  height: 0.85rem;
  width: 0.85rem;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
}
.song-box-but i{
  background: url(../assets/image/icon-ellipses.png) no-repeat;
  background-size: 100% 100%;
  width: 0.67rem;
  height: 0.17rem;
}
/*song-list-box end*/



