
.music-charts{
  position: absolute;
  top:1.29rem;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-direction: column;
  flex-direction: column;
}
.music-charts-item{
  margin-bottom: 0.24rem;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  background: hsla(0,0%,100%,.1);
  overflow: hidden;
  position: relative;
}
/*music-chart-img start*/
.music-charts-img{
  width: 3.41rem;
  height: 3.41rem;
}
.music-charts-img img{
  width: 100%;
}
/*music-charts-img end*/
/*music-charts-list start*/
.music-charts-list{
  height: 2.8rem;
  padding: 0.29rem;
  -o-flex: 1;
  -moz-flex: 1;
  -ms-flex: 1;
  flex: 1;
  overflow: hidden;
}
.music-charts-title{
  color: #fff;
  font-size: 0.37rem;
  padding-bottom: 0.12rem;
}
.music-charts-box{
  padding: 0.12rem 0;
  color: #fff;
  font-size: 0.29rem;
}
.music-charts-info{
  width: 100%;
  font-size: 0.29rem;
  color: #fff;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 0.29rem;
}
.music-charts-box .song-index{
  color: #c2c8d3;
  margin-right: 0.29rem;
  line-height: 0.29rem;
}
.music-charts-box .singer-name{
  margin-left: 0.29rem;
  color: #c2c8d2;
  line-height: 0.29rem;
}
.song-list-amount{
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  margin-top: 0.23rem;
}
.song-list-amount i{
  width: 0.26rem;
  height: 0.35rem;
  background: url(../assets/image/icon-song-listenCount.png) no-repeat;
  background-size: 100%;
  opacity: .7;
}
.song-list-amount .amount-num{
  font-size: 0.29rem;
  color: #fff;
  line-height: 0.29rem;
  margin-left: 0.23rem;
}
/*music-charts-list end*/
.music-charts-item::after{
  content: "";
  position: absolute;
  right: 0.15rem;
  width: 0.34rem;
  height: 0.61rem;
  top: 50%;
  transform: translate(-50%,-50%);
  background: url(../assets/image/icon--chevron-right.png) no-repeat;
  background-size: 100% 100%;
}




