


























































































































































/*header start*/
.search-wrap{
  width: 100%;
  height: 1.29rem;
}
.header{
  width: 100%;
  height: 1.29rem;
  position: fixed;
  z-index: 999;
}
.wyl-icon{
  display: block;
  width: auto;
  height: 0.49rem;
}
.wyl-icon img{
  height: 100%;
}
.header .search-menu,.header .search-back{
  width: 1.37rem;
  padding: 0.39rem 0 0 0.24rem;
  float: left;
  box-sizing: border-box;
}
.header .search-back i{
  width: 0.61rem;
  height: 0.61rem;
}
.header .search-input{
  width: 7.27rem;
  height: 0.83rem;
  float: left;
  margin-top: 0.24rem;
}
.header .search-input .wyl-input{
  width: 100%;
  height: 100%;
  border: 0.02rem solid hsla(0,0%,100%,.1);
  padding: 0.12rem 0.24rem;
  background: hsla(0,0%,100%,.3);
  color: #fff;
  font-size: 0.34rem;
  box-sizing: border-box;
  border-radius: 0.12rem;
}
.header .search-button{
  padding: 0.34rem 0 0 0.5rem;
  float: left;
}
.header .search-button img{
  height: 0.63rem;
}
/*header end*/
/*search-list start*/
.search-list-title{
  height: 0.88rem;
  padding: 0 0.44rem;
  font-size: 0.34rem;
  color: #fff;
  background: hsla(0,0%,100%,.1);
  line-height: 0.88rem;
}
.search-list-pic img{
  width: 0.78rem;
  height: 0.78rem;
}
/*search-list end*/
/*search-hot start*/
.search-hot,.search-list{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  overflow-y: scroll;
  padding-bottom: 1.56rem;
  z-index: 11;
  padding-top: 1.29rem;
  background: url(../assets/image/body-bg.7f43813.jpg) no-repeat 100%;
  background-attachment: fixed;
  background-size: 100% 100%;
}
.search-hot-box,.search-list-box{
  padding: 0.44rem;
  background: url(../assets/image/count-box-border.png) no-repeat bottom;
  background-size: 100% auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}
.search-hot-box span,.search-list-box span{
  font-size: 0.34rem;
  color: #c2c8d2;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.search-hot-box span.search-hot-name,.search-list-box span.search-list-name{
  color: #fff;
  margin: 0 0.29rem;
}

/*search-hot end*/



